@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

.cityhall-panel {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60%;
  height: 70%;
  background-color: #101418;
  border-radius: 10px;
  transform: translate(-50%, -50%);
}

.cityhall-panel__header {
  display: flex;
  width: 100%;
  height: 8%;
  background-color: #0c0e11;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  justify-content: space-between;
  align-items: center;
}

.cityhall-panel__title {
  text-transform: uppercase;
  font-family: 'Poppins', sans-serif;
  font-weight: 900;
  font-size: 1.6rem;
  text-align: center;
  align-items: center;
  vertical-align: middle;
  color: gray;
}

.button-back {
  color: gray;
  font-size: 30px;
  width: fit-content;
  margin-left: 15px;
  /* transition: 1s; */
}

.button-back:hover {
  color: gray;
  cursor: pointer;
}

.button-back-hidden {
  color: transparent;
  font-size: 30px;
  width: fit-content;
  margin-left: 15px;
  /* transition: 1s; */
}

.button-close {
  color: gray;
  font-size: 30px;
  width: fit-content;
  margin-right: 15px;
  /* transition: 1s; */
}

.button-close:hover {
  color: gray;
  cursor: pointer;
}

.container {
  display: flex;
  position: relative;
  height: 92%;
  border-radius: 10px;
  flex-direction: row;
}

.slider {
    position: relative;
    /* background: wheat; */
    height: 100%;
    width: 25%;
    left: 0.1%;
    border-radius: 5px;
    border-right: 3px solid #0c0e11;
}

.button {
  text-align: center;
  color: black;
  background: white;
  height: 10%;
}

.char-info {
  background: #0c0e11;
  color: white;
  font-size: 20px;
  display: flex;
  flex-direction: row;
  height: 10%;
  margin-left: 5%;
}

.data {
  display: flex;
  flex-direction: column !important;
  padding: 5%;
}

.cid {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 15px;
  color: #9494948f;
}

.name {
  font-weight: 500;
  text-transform: uppercase;
  font-size: 20px;
  font-family: 'Poppins', sans-serif;
}

.MuiList-root {
  color: white !important;
  background-color: #0c0e11 !important; 
  font-family: 'Poppins', sans-serif !important;
  font-weight: 500 !important;
  /* position: relative !important;
  top: 20% !important; */
}

.MuiTypography-root {
  font-size: 1rem !important;
  text-transform: uppercase !important;
  margin-left: 10px !important;
}

.MuiListItem-root {
  padding-top: 16px !important;
  padding-bottom: 16px !important;
}

.content {
  margin-top: 8px;
  width: 100%;
  height: 98%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #101418;
}

.main-section {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  overflow-y: scroll;
  max-height: 84%;
}

.main-section2 {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  overflow-y: scroll;
  max-height: 84%;
}

.card {
  display: flex;
  width: 340px;
  height: 280px;
  margin: 16px 16px 16px 16px;
  border-radius: 8px;
  background: #181b1d;
  box-sizing: border-box;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 10px;
}

.card-header {
  display: grid;
  grid-template-columns: 260px 0px;
  width: 100%;
}

.card-header2 {
  display: grid;
  grid-template-columns: 280px 0px;
  width: 100%;
}

.card-icon {
  display: flex;
  background: #26262a;
  width: 10%;
  height: 50%;
  justify-content: center;
  padding: 10px;
  color: white;
  border-radius: 5px;
}

.card-title {
  color: white;
  font-weight: 900;
  font-size: 1rem;
}

.card-desc {
  color: #80808085;
}

.card-button {
  display: flex;
  justify-content: center;
}

.card-tag {
  background: #26262a;
  width: max-content;
  height: max-content;
  text-align: center;
  font-weight: 900;
  font-size: 15px;
  border-radius: 3px;
  color: gray;
  padding: 5px;
}

.MuiButton-root {
  width: 98%;
  border-radius: 2px !important;
  padding: 3px 16px !important;
  font-size: 0.8rem !important;
}

.card2 {
  display: flex;
  width: 400px;
  height: 380px;
  margin: 110px 56px 16px 56px;
  border-radius: 8px;
  background: #181b1d;
  box-sizing: border-box;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 10px;
  /* transition: 0.4s; */
}

.card-icon2 {
  font-size: 180px;
  color: white;
  text-align: center;
}

.card-title2 {
  color: white;
  font-weight: 900;
  font-size: 1.5rem;
  letter-spacing: 2px;
  text-align: center;
}

.card2:hover {
  cursor: pointer;
  background: #303132;
}